<template>
  <div class="smart-service">
    <div class="list">
      <router-link to="/hospitalSign" tag="div" class="list-item"
        >住院登记</router-link>
      <router-link to="/outpatientApplication" tag="div" class="list-item"
        >门诊登记</router-link>
      <router-link to="/medicalService" tag="div" class="list-item"
        >医疗服务指南</router-link>
      <router-link to="/changeDesignatedHospital" tag="div" class="list-item"
        >定点医院变更</router-link>
      <router-link to="/hospitalNotJing" tag="div" class="list-item"
        >京外就医备案</router-link>
      <router-link to="/physicalPlan" tag="div" class="list-item"
        >体检预约</router-link>
      <router-link to="/installPhone" tag="div" class="list-item"
        >急救电话安装申请</router-link>
      <router-link :to="{
      name: 'workList',
        query: {
          title: '健康养老常识',
          method: 'jbxx1QuerydataJkylcsList'
        }
      }" tag="div" class="list-item">健康养老常识</router-link>
    </div>
  </div>
</template>

<script>
export default {
  methods: {}
};
</script>

<style lang="less" scoped>
.smart-service {
  min-height: 100%;
  box-sizing: border-box;
  font-size: 42px;
  color: #333333;
  padding: 24px;
  background: #f5f5f5;
  .list {
    .list-item {
      position: relative;
      background: #fff;
      height: 190px;
      line-height: 190px;
      box-shadow: 3px 4px 9px 1px rgba(1, 88, 155, 0.05);
      border-radius: 8px;
      font-weight: normal;
      font-stretch: normal;
      padding-left: 200px;
      &:not(:first-child) {
        margin-top: 10px;
      }
      background-repeat: no-repeat;
      background-position: 32px center;
      background-size: 123px 123px;
      @modeList: hospitalSign, outpatientApplication, medicalServiceDetail,
        changeDesignatedHospital, hospitalNotJing, healthCheckUpSign,
        installPhone, resource-for-elderly;
      .bgIcon(@n, @i:1) when (@i <= @n) {
        &:nth-child(@{i}) {
          @word: extract(@modeList, @i);
          background-image: url("../assets/images/@{word}.png");
        }
        .bgIcon(@n, (@i + 1));
      }
      .bgIcon(length(@modeList));
    }
  }
}
</style>



